import React, { useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

import eduData from "../../js/data/edu_data.json";
import "../../scss/pages/search.scss";

/********************************************************
 * [  ]
[v]        (edu_data.json -> idx)
[v]        
[v]    " "  
[v]       
********************************************************/

const SearchPage = () => {
  const { state } = useLocation();
  const keyword = state ? state.keyword : "";
  const navigate = useNavigate();

  //  
  const selData = eduData.filter((v) => {
    //       
    const lowerKeyword = keyword.toLowerCase();
    return (
      v.gName.toLowerCase().includes(lowerKeyword) || 
      v.gInfo.toLowerCase().includes(lowerKeyword)
    );
  });

  //   
  const formatPrice = (price) => (Number(price) === 0 ? "" : `${Number(price).toLocaleString()}`);

  return (
    <div className="search-result">
      {keyword === "" ? ( //   
        <div className="message-container">
          <h4 className="none-title"> .  .</h4>
          <button className="research"
          onClick={()=>{
            
            document.querySelector('.search').focus();
          }}
          > </button>
        </div>
      ) : selData.length === 0 ? ( //    
        <div className="message-container">
        <h4 className="none-title"><span>"{keyword}"</span>   .   .</h4>
        <button className="research"
        onClick={()=>{
          
          document.querySelector('.search').focus();
        }}
        > </button>
      </div>
      ) : (
        <>
          <h3 className="search-title">
            <span>"{keyword}"</span> .
          </h3>
          <section className="search-wrap">
            {selData.map((v) => (
              <div key={v.idx} className="search-edu" onClick={() => navigate(`/detail/${v.idx}`)}>
                <div className="search-image">
                  <img src={`./images/edu_thumb/${v.idx}.png`} alt={`  ${v.gName}`} />
                </div>
                <h3 className="search-tit">{v.gName}</h3>
                <p className="search-desc">: {v.gInfo}</p>
                <p>: {v.gLevel}</p>
                <p>: {formatPrice(v.gPrice)}</p>
                <p>: {v.gSkill}</p>
                <a className="cart-btn" href="#none">
                  <i className="fa-solid fa-cart-shopping"></i>
                </a>
              </div>
            ))}
          </section>
        </>
      )}
    </div>
  );
};

export default SearchPage;
