// ./src/components/pages/Login.jsx

import React, { useContext, useEffect, useState } from "react";
import "../../scss/pages/join.scss";
import "../../scss/pages/member.scss";
import { initData } from "../../js/func/mem_fn";
import { dCon } from "../modules/dCon";

function Login() {
  const myCon = useContext(dCon);
  console.log(" dCon:", myCon);

  //  
  const [userId, setUserId] = useState("");
  const [pwd, setPwd] = useState("");
  const [userIdError, setUserIdError] = useState(false);
  const [pwdError, setPwdError] = useState(false);

  //  
  const msgId = [" .", "  ."];

  const msgPwd = [" .", "  ."];

  //    
  const [idMsg, setIdMsg] = useState(msgId[0]);
  const [pwdMsg, setPwdMsg] = useState(msgPwd[0]);

  //   
  const changeUserId = (e) => {
    const val = e.target.value;
    setUserId(val);

    if (val) {
      setUserIdError(false);
      // setIdMsg(msgId[2]); //   
    } else {
      setIdMsg(msgId[0]);
      setUserIdError(true);
    }
  };

  //   
  const changePwd = (e) => {
    const val = e.target.value;
    setPwd(val);

    if (val) {
      setPwdError(false);
    } else {
      setPwdMsg(msgPwd[0]);
      setPwdError(true);
    }
  };

  //   
  const totalValid = () => {
    if (!userId) setUserIdError(true);
    if (!pwd) setPwdError(true);
    return userId && pwd && !userIdError && !pwdError;
  };

  //  
  const onSubmit = (e) => {
    e.preventDefault();
    console.log(":", totalValid());

    if (totalValid()) {
      console.log("/ ");
      initData();

      let memData = JSON.parse(localStorage.getItem("mem-data"));
      console.log(memData);

      const result = memData.find((v) => v.uid === userId);
      console.log(" ", result);

      if (!result) {
        setIdMsg(msgId[1]);
        setUserIdError(true);
      } else {
        setUserIdError(false);
        if (pwd === result.pwd) {
          sessionStorage.setItem("minfo", JSON.stringify(result));
          // myCon.setLoginSts(sessionStorage.getItem("minfo"));
          myCon.setLoginSts(result);
          myCon.makeMsg(result.unm);
          ///////////   //////////// 
          console.log(myCon.loginSts);
          document.querySelector(".log-submit").innerText = "    ";

          setTimeout(() => {
            myCon.goPage("/mypage");
          }, 1000);
        } else {
          setPwdMsg(msgPwd[1]);
          setPwdError(true);
        }
      }
    } else {
      alert(" /  .");
    }
  };

  //      
  useEffect(() => {
    document.querySelector("#user-id").focus();
  }, []);

  // 
  return (
    <div className="join-page login-wrap">
      <div className="join-top">
        <h2 className="join-title"></h2>
      </div>
      <ul className="slide-text">
        <li className="slide">    ? </li>
        <li className="slide">  . </li>
        <li className="slide">    </li>
      </ul>
      <form className="join-form" onSubmit={onSubmit}>
        <ul>
          <li className="join-id">
            <label></label>
            <input
              id="user-id"
              type="text"
              maxLength="15"
              placeholder=" ."
              value={userId}
              onChange={changeUserId}
            />
            {userIdError && (
              <div className="msg">
                <small style={{ color: "#fe5b16" }}>{idMsg}</small>
              </div>
            )}
          </li>
          <li>
            <label></label>
            <input
              type="password"
              maxLength="20"
              placeholder=" ."
              value={pwd}
              onChange={changePwd}
            />
            {pwdError && (
              <div className="msg">
                <small style={{ color: "#fe5b16" }}>{pwdMsg}</small>
              </div>
            )}
          </li>
          <li style={{ overflow: "hidden" }}>
            <button className="log-submit" type="submit" onClick={onSubmit}>
              
            </button>
          </li>
        </ul>
      </form>
    </div>
  );
}

export default Login;
