// DC PJ     - Read.jsx

import React, {useContext, useEffect, useRef, useState} from "react";
import {dCon} from "../dCon";

//   ////
import $ from "jquery";

function Read({setMode, selRecord}) {
  // setMode -    setter
  // selRecord -  

  //    
  const selData = selRecord.current;

  //   API !!
  const myCon = useContext(dCon);
  // console.log('Read loginSts:',myCon.loginSts);

  // [   ] ////
  // 1 :    !
  // 2 :   !
  // 3 :    !

  // ((     ))
  // ->  !   !
  // ->     !
  // ->    !

  // 1.     ///
  // bd-rec board-record  
  if (!sessionStorage.getItem("bd-rec")) {
    sessionStorage.setItem("bd-rec", "[]");
  } //// if ////

  // 2.    ////

  // (1)    ///
  let rec = JSON.parse(sessionStorage.getItem("bd-rec"));

  // (2)      
  //  true   !
  // .includes() ->   true
  let isRec = rec.includes(selData.idx);
  // console.log(':',selData.idx);
  // console.log(':',rec);
  // console.log('?',isRec);

  // (3)    isRec true!
  //    myCon.loginSts !
  if (myCon.loginSts) {
    //     true !
    // console.log(
    //   ' :', selData.uid,
    //   ' :', myCon.loginSts.uid
    // );
    //     ?
    if (selData.uid === myCon.loginSts.uid) {
      //     isRec
      // true 
      isRec = true;
    } //// if ////
  } //// if /////

  // (4)    :   !
  if (!isRec) rec.push(selData.idx);
  // ->    !

  // (5)    ////
  sessionStorage.setItem("bd-rec", JSON.stringify(rec));

  // 3.   ///////////////
  // ->     
  if (!isRec) {
    // (1)    
    let bdData = JSON.parse(localStorage.getItem("board-data"));
    // console.log('!',bdData);

    // (2)    cnt 
    //  : isRec false( if !)
    bdData.some((v) => {
      if (v.idx === selData.idx) {
        // console.log('!',v.cnt);
        //  1 
        v.cnt = Number(v.cnt) + 1;
        //  true 
        return true;
      } /// if ///
    }); ///// some /////

    // (3)    ///
    localStorage.setItem("board-data", JSON.stringify(bdData));
  } ///// if ///////

  //  ////
  // [    ] ////////////
  // [1]     
  // (1)    
  const [commentData, setCommentData] = useState([]);
  // (2) TextArea  
  const textareaRef = useRef([]);
  // (3)   idx  :  null !
  const [isEditing, setIsEditing] = useState(null);
  // (4)    
  const [editedContent, setEditedContent] = useState("");

  /***************************************** 
      [     ]
        idx: unique key,
        cont: comment content,
        uid: user id,
        unm: user name,
        bid: board id,
        date: comment date,
        
      [   : comment-data ]
   *****************************************/

  // [2]     ///////
  // (1)    //////
  const saveComment = () => {
    // 1)     
    if ($(".comment-box").val().trim() === "") {
      alert("Write comment!");
      return;
    } /// if ////

    // 2)    
    // ->        !
    let comDt = localStorage.getItem("comment-data") ? JSON.parse(localStorage.getItem("comment-data")) : [];

    console.log(" ~!", comDt);

    // 3)      
    //  idx  ?
    // ->  idx  max   1
    // -> comDt   null  1 !
    comDt.push({
      idx: comDt.length > 0 ? Math.max(...comDt.map((v) => v.idx)) + 1 : 1, // 
      cont: $(".comment-box").val(),
      uid: myCon.loginSts.uid, //   
      unm: myCon.loginSts.unm, //   
      bid: selData.idx, //    
      date: new Date().toJSON().substring(0, 10), // 
    });
    // 4)    
    localStorage.setItem("comment-data", JSON.stringify(comDt));
    // 5)    
    $(".comment-box").val("");
    // 6)    !
    makeCommentData();
  }; //////////// saveComment  /////////////

  // (2)     
  const makeCommentData = () => {
    if (localStorage.getItem("comment-data")) {
      //     
      let temp = localStorage.getItem("comment-data");
      temp = JSON.parse(temp);
      temp = temp
        //     
        .filter((v) => v.bid === selData.idx)
        //  + idx
        .sort((a, b) =>
          a.date > b.date
            ? -1
            : a.date < b.date
            ? 1
            : //  :  ?
            a.date !== b.date
            ? //   0
              0
            : //    ?
            // idx /
            a.idx > b.idx
            ? -1
            : a.idx < b.idx
            ? 1
            : 0
        ); /// filter + sort /////////

      //    
      setCommentData(temp);
    }
  }; //// makeCommentData  ////

  // (3)      !
  const adjustHeight = () => {
    //   textarea    !!
    //    index  !
    textareaRef.current.forEach((textarea, index) => {
      // index  
      if (textarea) {
        // console.log(":", textarea.scrollHeight);
        //       !
        textarea.style.height = "auto";
        //    !
        textarea.style.height = `${textarea.scrollHeight}px`;
      } /// if ///
      //   else ( !!!)
      else {
        //    
        console.log("Textarea  :", index);
      } /// else ///
    });
  }; ////

  // [4]  
  const deleteComment = (idx) => {
    // (1)    "" 
    if (!window.confirm("Are you sure you want to delete?")) return;

    // (2) idx  filter  localStrage  
    let comDt = JSON.parse(localStorage.getItem("comment-data"));
    // idx  idx     !
    comDt = comDt.filter((v) => v.idx !== idx);
    //   !
    localStorage.setItem("comment-data", JSON.stringify(comDt));
    // (3)    !
    makeCommentData();
  }; //// deleteComment  ////

  // [5]    
  const modifyComment = (idx) => {
    // (1)  
    setIsEditing(idx);
    // (2) idx   
    const selData = commentData.find((v) => v.idx === idx);
    // (3)     editedConTent 
    setEditedContent(selData.cont);
    //  ?       !
  }; ////
  // [6]   
  const saveModifiedComment = (idx) => {
    // (1)      
    let comDt = JSON.parse(localStorage.getItem("comment-data"));

    // (2)     cont 
    comDt = comDt.map((v) => (v.idx === idx ? {...v, cont: editedContent} : v));

    // (3)   !
    localStorage.setItem("comment-data", JSON.stringify(comDt));

    // (4)    !
    setIsEditing(null); //   send -> Modify 

    // (5)    
    makeCommentData();
  }; ////

  //      !
  useEffect(() => {
    adjustHeight();
  }, [commentData]);

  //   
  useEffect(() => {
    //    
    makeCommentData();
  }, []); //// useEffect ////

  //////////////////////////////////
  //   ///////////////////
  return (
    <>
      <h1 className="tit"></h1>
      <table className="dtblview readone">
        <caption></caption>
        <tbody>
          <tr>
            <td></td>
            <td>
              <input type="text" className="name" size="20" readOnly={true} defaultValue={selData.unm} />
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <input type="text" className="subject" size="60" readOnly={true} defaultValue={selData.tit} />
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <textarea className="content" cols="60" rows="10" readOnly={true} defaultValue={selData.cont}></textarea>
            </td>
          </tr>          
        </tbody>
      </table>

      <br />
      {/*   */}
      <table className="dtbl btngrp">
        <tbody>
          <tr>
            <td>
              <button
                onClick={() => {
                  //  ('L') 
                  setMode("L");
                }}>
                
              </button>
              {
                //      
                //  
                myCon.loginSts && myCon.loginSts.uid === selData.uid && (
                  <button
                    onClick={() => {
                      //  
                      setMode("M");
                    }}>
                    
                  </button>
                )
              }
            </td>
          </tr>
        </tbody>
      </table>
      {/*    */}
      {
            //     !
            myCon.loginSts && (
              <table className="dtblview">
            <tbody>
              <tr>
                <td></td>
                <td>
                  <textarea className="comment-box" cols="60" rows="5"></textarea>
                  <button
                    style={{
                      verticalAlign: "35px",
                    }}
                    onClick={saveComment}>
                    
                  </button>
                </td>
              </tr>
               </tbody>
               </table>
            )
          }
      {/*     */}
      {
        //   ( !)
        commentData && (
          <table className="dtblview comment">
            <tbody>
              {
                //    
                commentData.map((v, i) => (
                  <tr key={i}>
                    {/* (1)    */}
                    <td>
                      {v.unm}
                      <br />
                      {myCon.loginSts && myCon.loginSts.uid === v.uid && (
                        <div style={{
                          display: 'flex',
                          justifyContent: 'center',
                        }}>
                          <button
                            //   idx  !
                            onClick={() => deleteComment(v.idx)}>
                            <i className="fa-solid fa-trash"></i>
                          </button>

                          {/*   'send' */}
                          {
                            //  isEditing v.idx  
                            isEditing === v.idx ? (
                              /* Send      */
                              <button
                                onClick={() => {
                                  saveModifiedComment(v.idx);
                                }}>
                                <i className="fa-solid fa-comment"></i>
                              </button>
                            ) : (
                              <button
                                /* Modify  isEditing   */
                                onClick={() => {
                                  modifyComment(v.idx);
                                }}>
                                <i className="fa-solid fa-pencil"></i>
                              </button>
                            )
                          }
                        </div>
                      )}
                    </td>
                    {/* (2)  
                    -> textarea   !
                    */}
                    <td style={{
                      width:"80%",
                    }}>
                      <textarea
                        className="comment-view-box"
                        //      
                        //   textareaRef !
                        ref={(el) => (textareaRef.current[i] = el)}
                        value={
                          isEditing === v.idx // 
                          ? editedContent // 
                          : v.cont //    
                        }
                        //    !
                        readOnly={isEditing !== v.idx}
                        //    onChange !
                        onChange={(e)=>{ // e -  
                          //    
                          if(isEditing === v.idx)
                            setEditedContent(e.target.value);
                          //    
                          // value   
                          //   !!!
                        }}
                        style={{
                          width: "100%",
                          border: "none",
                          //  
                          outline: 
                          isEditing === v.idx
                          ? "2px solid green" //  
                          : "none", //  
                          overflow: "hidden",
                          resize: "none",
                        }}></textarea>
                    </td>
                    {/* (3)   */}
                    <td style={{
                      width: "10%",
                      fontSize: "1.4rem",
                    }}>
                      {v.date}
                    </td>
                  </tr>
                ))
              }
            </tbody>
          </table>
        )
      }
    </>
  );
}

export default Read;
