///   : Layout.jsx ///

import Footer from "./FooterArea";
import {Header} from "./Header";
import MainArea from "./MainArea";

//  API   ! ////
import { dCon } from "../modules/dCon";
import { useLocation, useNavigate } from "react-router-dom";
import { useCallback, useEffect, useState } from 'react';

export default function Layout() {
  // [     ] ////
  // [1]  
  // const [loginSts, setLoginSts] = useState(sessionStorage.getItem("minfo"));
  // [1]   : 
  //  -   !
  const [loginSts, setLoginSts] = useState(
    sessionStorage.getItem("minfo")
      ? JSON.parse(sessionStorage.getItem("minfo"))
      : null
  );
  // ->   'minfo' !
  console.log(loginSts);

  // [2]     ////
  const [loginMsg, setLoginMsg] = useState(null);


  // [3] 
  const [force, setForce] = useState(true);

  // [  ] /////////////////
  // [1]   ////
  const goNav = useNavigate();

  //   : (  !) //////
  const goPage = useCallback((pm1,pm2)=>{
    // pm1 - , pm2 - state 
    goNav(pm1, pm2);
  },[]);

  // [2]     ///
  const makeMsg = (name) => {
    // 
    let usrIcon = ["", "", "", "", ""];
    //  : 0~4 
    let rdm = Math.floor(Math.random() * 5);
    //    
    setLoginMsg(`Welcome ${name} ${usrIcon[rdm]}`);
  }; ///////// makeMsg /////////////

  // [3]  
  const logoutFn = () => {
    // (1)   null
    setLoginSts(null);
    // (2)   : minfo
    sessionStorage.removeItem('minfo');
    // (3)   
    setLoginMsg(null);
    // (4)   
    goPage("./");
  }; /// logoutFn ///

  //   ////
  return (
    <dCon.Provider
      value={{
        loginSts, //  getter
        setLoginSts, //  setter
        loginMsg, //  getter
        makeMsg, //  
        goPage, //  
        logoutFn, //  
      }}>
      <Header 
      goPage={goPage} 
      loginSts={loginSts} 
      setLoginSts={setLoginSts}
      />
      <MainArea />
      <Footer />
    </dCon.Provider>
  );
} //// Layout  ////
