import {memo, useEffect, useState} from "react";
import {Link} from "react-router-dom";
import "../../scss/header.scss";
import $ from "jquery";

const Header = memo(({goPage, loginSts, setLoginSts}) => {
  const [searchKeyword, setSearchKeyword] = useState("");
  const eduCate = ["", "", "", "", "", "", ""]; //  

  useEffect(() => {
    const $mainMenu = $(".main-category > li:eq(0) > a");
    const $subMenu = $(".sub-category");

    //      
    $mainMenu.on("click", function (e) {
      e.preventDefault();
      $subMenu.toggleClass("on");
    });
    $subMenu.on("mouseleave", function (e) {
      $subMenu.removeClass("on");
    });

    //      
    $(document).on("click", function (e) {
      if (
        !$mainMenu.is(e.target) && //    
        !$subMenu.is(e.target) && //    
        $subMenu.has(e.target).length === 0 //    
      ) {
        $subMenu.removeClass("on");
      }
    });

    //      
    return () => {
      $mainMenu.off("click");
      $(document).off("click");
    };
  }, []);

  const handleSearch = () => {
    goPage("search", { state: { keyword: searchKeyword } });
  };

  // 
  return (
    <header>
      <div className="header">
        <ul>
          <div>
            <h1 className="logo">
              <a href={process.env.PUBLIC_URL}>
                <img src={`${process.env.PUBLIC_URL}/images/main/inflearn_logo.png`} alt="logo" />
              </a>
            </h1>
          </div>
          <div className="top-category">
            <ul className="main-category">
              <li>
                <a href="#"></a>
              </li>
              <li>
                <Link to="/board"></Link>
              </li>
            </ul>
            <ul className="sub-category">
              {eduCate.map((category) => (
                <li key={category}>
                  <a href={`${process.env.PUBLIC_URL}/#${category}`}>{category}</a>
                </li>
              ))}
            </ul>
          </div>
          <div className="member-nav">
            {!loginSts && (
              <ul>
                <li>
                  <Link to="/login">
                    <i className="fa-solid fa-arrow-right-to-bracket" title=""></i>
                  </Link>
                </li>
                <li>
                  <Link to="/join">
                    <i className="fa-solid fa-user-plus" title=""></i>
                  </Link>
                </li>
                <li>
                  <Link to="/mypage">
                    <i className="fa-solid fa-user" title=""></i>
                  </Link>
                </li>
                <li>
                  <Link to="/cartlist">
                    <i className="fa-solid fa-cart-shopping" title=""></i>
                  </Link>
                </li>
              </ul>
            )}
            {loginSts && (
              <ul>
                <li>
                  <a
                    href="#"
                    onClick={(e) => {
                      e.preventDefault();
                      setLoginSts(null);
                      sessionStorage.removeItem("minfo");
                      goPage("/");
                    }}
                    className="logout">
                    <i className="fa-solid fa-arrow-up-from-bracket" title=""></i>
                  </a>
                </li>
                <li>
                  <Link to="/mypage">
                    <i className="fa-solid fa-user" title=""></i>
                  </Link>
                </li>
                <li>
                  <Link to="/cartlist">
                    <i className="fa-solid fa-cart-shopping" title=""></i>
                  </Link>
                </li>
              </ul>
            )}
          </div>
          <div className="search-area">
            <input id="search" className="search" type="text"
            value={searchKeyword}
            onChange={(e) => setSearchKeyword(e.target.value)}
            onKeyUp={(e) => {
              e.preventDefault();
              if (e.key === "Enter") {
                handleSearch();
              }
            }}
            />
            <button className="sbtn" onClick={handleSearch}></button>
          </div>
        </ul>
      </div>
    </header>
  );
});

export {Header};
