import React, {useContext} from "react";
import {dCon} from "../dCon";

//   ////
import $ from "jquery";

function Write({setMode, totalCount, setPageNum, pgPgNum}) {
  // setMode -    setter
  // totalCount -    (  1!)
  // setPageNum -   setter (  )
  // pgPgNum -    (  1)

  //   API !!
  const myCon = useContext(dCon);
  //   console.log("Write loginSts:", myCon.loginSts);

  //     //////
  const submitFn = () => {
    // 
    let title = $(".subject").val().trim();
    // 
    let content = $(".content").val().trim();
    // trim()   !

    // (1)  
    // - ,    !
    if (title === "" || content === "") {
      alert("Insert title and content!");
      return;
    } /// if /////

    // (2)   //////
    else {
      // 1)   ////////////
      // 1-1)    
      let localData = localStorage.getItem("board-data");

      // 1-2) JSON.parse()  
      localData = JSON.parse(localData);

      // 1-3)   idx 
      let totalIdx = localData.map((v) => v.idx);
      //   console.log("idx :", totalIdx);

      // 1-4) idx    :
      //   ...totalIdx ->  max 
      let maxIdx = Math.max(...totalIdx);
      //   console.log("idx :", maxIdx);

      // 2)   ///////////
      let today = new Date();
      //   console.log(today);
      // toJSON()  (yyyy-MM-dd)
      // ->  10  : substr(,)
      today = today.toJSON().substr(0, 10);
      //   console.log(today);

      // [ idx    ] ///
      // idx  idx 1 
      //    
      // Number() !

      // 3)    
      let data = {
        idx: Number(maxIdx) + 1,
        tit: title,
        cont: content,
        att: "",
        date: today,
        uid: myCon.loginSts.uid,
        unm: myCon.loginSts.unm,
        cnt: 0,
      };
      console.log(":", data);

      // 4)       
      localData.push(data);

      // 5)    
      localStorage.setItem("board-data", JSON.stringify(localData));

      // 6)    1
      totalCount.current++;

      // 7)   
      setPageNum(1);

      // 8)    
      pgPgNum.current = 1;

      // 9)     
      setMode("L");
    } /// else /////
  }; ////////// submitFn  //////////////

  //   ///////////////////
  return (
    <>
      <h1 className="tit"></h1>
      <table className="dtblview readone">
        <caption></caption>
        <tbody>
          <tr>
            <td></td>
            <td>
              <input
                type="text"
                className="name"
                size="20"
                readOnly={true}
                //  
                defaultValue={myCon.loginSts.unm}
              />
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <input type="text" className="subject" size="60" />
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <textarea className="content" cols="60" rows="10"></textarea>
            </td>
          </tr>
          {/* <tr>
            <td>Attachment</td>
            <td></td>
          </tr> */}
        </tbody>
      </table>
      <br />
      <table className="dtbl btngrp">
        <tbody>
          <tr>
            <td>
              <button onClick={submitFn}></button>
              <button
                onClick={() => {
                  //  ('L') 
                  setMode("L");
                }}>
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </>
  );
}

export default Write;
