// DC PJ     - List.jsx

import React, { Fragment, useContext } from "react";
import { dCon } from "../dCon";

function List({
  selData, //   
  setMode, //    setter
  selRecord, //  
  pageNum, //   getter
  setPageNum, //   setter
  unitSize, //  
  totalCount, //   
  pgPgSize, //   
  pgPgNum, //   

  searchFn, // 
  keyword, //   getter
  setKeyword, //   setter
  order, //  
  setOrder, //   setter
  sortCta, //   getter
  setSortCta, //   setter
}) {
  //   API !!
  const myCon = useContext(dCon);
  // console.log('List loginSts:',myCon.loginSts);

  // [     ] ////

  // 1.   :   /  
  // ->    !
  let pagingCount = Math.floor(totalCount.current / unitSize);
  console.log("  /  :", pagingCount);
  console.log(":", totalCount.current % unitSize);

  // 2.     1!
  //  %  = 0   !
  if (totalCount.current % unitSize > 0) {
    pagingCount++;
  } /// if ///

  // 3.    
  // :    /   
  // pagingCount / pgPgSize
  let pgPgLimit = Math.floor(pagingCount / pgPgSize);

  //      1 
  if (pagingCount % pgPgSize > 0) {
    pgPgLimit++;
  } /// if ///

  console.log("  :", pgPgLimit);

  /*********************************** 
          
  ***********************************/
  const pagingCode = () => {
    // [ (1)     ]
    // ->  JSX  
    //      !
    let hcode = [];

    // [ (2)  for ,   ]
    // [1]  :  * (-1)
    let initNum = pgPgSize * (pgPgNum.current - 1);
    // [2]  :  * 
    let limitNum = pgPgSize * pgPgNum.current;
    // :pgPgNum  pgPgNum.current !

    // (( :  )) : pgPgSize  3 
    // for (let i = 0; i < 3; i++){} -> 1,2,3
    // for (let i = 3; i < 6; i++){} -> 4,5,6
    // for (let i = 6; i < 9; i++){} -> 7,8,9
    // for (let i = 9; i < 12; i++){} -> 10,11,12

    // [ (3)       ]
    //   1  !!!
    // pgPgNum  current !
    if (pgPgNum.current !== 1)
      hcode.push(
        <Fragment key="-1">
          {/*    */}
          <a
            href="#"
            title="First Paging Section"
            onClick={() => {
              // (1)    !
              pgPgNum.current = 1;
              // (2)     !
              setPageNum(1);
            }}
          >
           <i className="fa-solid fa-backward"></i>
          </a>
          {/*    */}
          <a
            href="#"
            title="Previous Paging Section"
            onClick={() => {
              // (1)   
              pgPgNum.current--;
              // (2)     
              //   (!)
              setPageNum(initNum - (pgPgSize - 1));
              //    (-(-1)) !
            }}
          >
            <i className="fa-solid fa-arrow-left"></i>
          </a>
        </Fragment>
      );

    // [ (4) for    ] ////
    //    key  !
    //     <Fragment> !
    for (let i = initNum; i < limitNum; i++) {
      // (( !!! ))
      //    for !!!
      // , pagingCount    !
      if (i + 1 > pagingCount) break;

      //    ////
      hcode.push(
        <Fragment key={i}>
          {
            //   
            // a  b !
            i + 1 === pageNum ? (
              <b>{i + 1}</b>
            ) : (
              <a
                href="#"
                onClick={() => {
                  //  
                  setPageNum(i + 1);
                }}
              >
                {i + 1}
              </a>
            )
          }
          {
            //    (|) !
            //      !
            i < limitNum - 1 && i + 1 !== pagingCount && ""
          }
        </Fragment>
      );
    } //////////// for ////////////

    // [ (5)       ]
    //  :      
    if (pgPgNum.current !== pgPgLimit)
      hcode.push(
        <Fragment key="-2">
          {/*    */}
          <a
            href="#"
            title="Next Paging Section"
            onClick={() => {
              // (1)   
              pgPgNum.current++;
              // (2)     
              //   (!)
              setPageNum(limitNum + 1);
              //    (+1) !
            }}
          >
           
            <i className="fa-solid fa-arrow-right"></i>
          </a>
          {/*    */}
          <a
            href="#"
            title="Last Paging Section"
            onClick={() => {
              // (1)    !
              pgPgNum.current = pgPgLimit;
              // (2)     
              //   (!)
              setPageNum((pgPgLimit - 1) * pgPgSize + 1);
              //   
              //    (pgPgLimit-1)
              //    
              //  1    !
            }}
          >
            
            <i className="fa-solid fa-forward"></i>
          </a>
        </Fragment>
      );

    return hcode;
  }; //////////// pagingCode  /////////

  //      !
  //   map 
  // Array.from({length:})
  // ->   !
  // Array.from({ length: pagingCount }).map((v, i) => ())

  //   ////////////////////
  return (
    <>
      <h1 className="tit"></h1>
      <div className="selbx">
        <select name="cta" id="cta" className="cta">
          <option value="tit"></option>
          <option value="cont"></option>
          <option value="unm"></option>
        </select>
        
        <input
          id="stxt"
          type="text"
          maxLength="50"
          defaultValue={keyword.kw}
          onKeyUp={(e) => {
            //    ///
            if (e.key === "Enter") e.target.nextElementSibling.click();
            //     !

            // ,   
            setPageNum(1);
            pgPgNum.currnt = 1;
          }}
        />
        <button className="sbtn" onClick={searchFn}>
          
        </button>       

        {/*   */}
        <select
          name="sort_cta"
          id="sort_cta"
          className="sort_cta"
          value={sortCta}
          onChange={(e) => {
            //  
            setSortCta(e.target.value);
            //   
            e.target.value = sortCta;
            //   
            setPageNum(1);
            //   
            pgPgNum.current = 1;
          }}
        >
          <option value="date"></option>
          <option value="tit"></option>
        </select>
        <select
          name="sel"
          id="sel"
          className="sel"
          value={order}
          onChange={(e) => {
            //   
            setOrder(order * -1);
            //    
            e.target.value = order;
            //   
            setPageNum(1);
            //   
            pgPgNum.current = 1;
          }}
        >
          <option value="1"></option>
          <option value="-1"></option>
        </select>
      </div>
      <table className="dtbl" id="board">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {selData.map((v, i) => (
            <tr key={i}>
              <td>
                {
                  //   
                  // ->  + ( * (-1))
                  i + 1 + unitSize * (pageNum - 1)
                }
              </td>
              <td>
                <a
                  href="#"
                  onClick={(e) => {
                    // 
                    e.preventDefault();
                    // ('R') 
                    setMode("R");
                    //    
                    selRecord.current = v;
                  }}
                >
                  {v.tit}
                </a>
              </td>
              <td>{v.unm}</td>
              <td>{v.date}</td>
              <td>{v.cnt}</td>
            </tr>
          ))}
        </tbody>
        {/*   */}
        <tfoot>
          <tr>
            <td colSpan="5" className="paging">
              {pagingCode()}
            </td>
          </tr>
        </tfoot>
      </table>
      <br />
      <table className="dtbl btngrp">
        <tbody>
          <tr>
            <td>
              {
                //   
                myCon.loginSts && (
                  <button
                    onClick={() => {
                      //   
                      setMode("W");
                    }}
                  >
                    Write
                  </button>
                )
              }
            </td>
          </tr>
        </tbody>
      </table>
    </>
  );
}

export default List;
