import React, { useEffect, useRef, useState } from "react";

//   ///
import $ from "jquery";

//  CSS  ////
import "../../scss/pages/board.scss";

//   JS ////
import { initBoardData } from "../../js/func/board_fn";
import List from "../modules/board/List";
import Read from "../modules/board/Read";
import Write from "../modules/board/Write";
import Modify from "../modules/board/Modify";
import { useLocation } from "react-router-dom";

function Board() {
  // Mypage.jsx  state  
  const location = useLocation();
  useEffect(() => {
    if (location.state) {
      setMode(location.state.mode);
      selRecord.current = location.state.selData;
    }
  }, [location.state]);

  // [     ! ] ////
  initBoardData();

  // [   ! :    ] //////
  const baseData = JSON.parse(localStorage.getItem("board-data"));

  // [      ] //////
  // [1]  /////
  const [mode, setMode] = useState("L");
  // (1)  (L) : List Mode
  // (2)  (R) : Read Mode
  // (3)  (W) : Write Mode
  // (4)  (M) : Modify Mode ()

  // [2]     ////
  const [pageNum, setPageNum] = useState(1);

  // [3]   :  {cta:,kw:}
  const [keyword, setKeyword] = useState({
    cta: "tit",
    kw: "",
  });
  // console.log("{cta:,kw:}", keyword);
  // cta - creteria / kw -  keyword

  // [4]    : (asc(-1) / desc(1))
  const [order, setOrder] = useState(1);

  // [5]    :  ->  DB 
  const [sortCta, setSortCta] = useState("date");
  //    desc!
  // sortCta sort Criteria () !

  // [      ] //////

  // [1]    :  
  const selRecord = useRef(null);
  // -> / .current  !
  // console.log(" :", selRecord);

  // [2]   ( )
  // ->     !
  const totalCount = useRef(baseData.length);
  // console.log(":", totalCount);

  // [3]   
  const pgPgNum = useRef(1);
  // ->    ?
  //    
  //   
  //  !     !

  // [    ] //////
  const initVariables = () => {
    setMode("L");
    setPageNum(1);
    setKeyword({ cta: "tit", kw: "" });
    setOrder(1);
    setSortCta("date");
    // selRecord.current = null;
    // totalCount.current = baseData.length;
    pgPgNum.current = 1;
  }; ////////// initVariables  //////

  // [     ] ///////////
  // ->>>     

  // [1]   :  
  const unitSize = 7;
  // [2]    :    
  const pgPgSize = 3;

  // [    ]
  // ->    
  // -> pageNum, unitSize  

  //   :  * (-1)
  let initNum = unitSize * (pageNum - 1);
  //   :  * 
  let limitNum = unitSize * pageNum;
  //  ( 5, 1~3)
  // (5*(1-1)) = 0 /  (5*1) = 5
  // (5*(2-1)) = 5 /  (5*2) = 10
  // (5*(3-1)) = 10 /  (5*3) = 15

  //  //
  //  [    ]  //
  //  //

  //    
  let finalData;

  // [      ] /////////////
  // [1]    ////////
  if (keyword.kw !== "") {
    finalData = baseData
      // ((1))-> sortCta  
      //  -1 * order 1 
      //  -1 * order -1 
      //
      .sort((a, b) =>
        a[sortCta] > b[sortCta] || a.idx > b.idx
          ? -1 * order
          : a[sortCta] < b[sortCta] || a.idx < b.idx
          ? 1 * order
          : //  :  ?
          a[sortCta] !== b[sortCta]
          ? //   0
            0
          : //    ?
          // idx /
          a.idx > b.idx
          ? -1 * order
          : a[sortCta] < b[sortCta]
          ? 1 * order
          : 0
      )
      //    
      .filter((v) => {
        if (
          v[keyword.cta].toLowerCase().indexOf(keyword.kw.toLowerCase()) !== -1
        )
          return true;
      }); ////// filter ////////
  } ///// if :    /////////
  else {
    finalData = baseData
      // ((1))-> sortCta  
      //  -1 * order 1 
      //  -1 * order -1 
      //
      .sort((a, b) =>
        a[sortCta] > b[sortCta] 
          ? -1 * order
          : a[sortCta] < b[sortCta]
          ? 1 * order
          //  :  ?
          : a[sortCta] !== b[sortCta]
          //   0
          ? 0
          //    ?
          // idx /
          : a.idx > b.idx
          ? -1 * order
          : a.idx < b.idx
          ? 1 * order : 0
      );
  } ///// else :    ////////

  //      /////
  totalCount.current = finalData.length;

  // console.log("slice  /", initNum, "/", limitNum);

  // [ slice()      ]
  const selData = finalData.slice(initNum, limitNum);
  //   slice(, )
  // (1)  :    
  // (2)  :     
  // (3) slice  :
  //    1)    !
  //    2)       
  //         !( break!)

  // [     : for   ] ///
  // const selData = [];

  // [        ] ///
  // for (let i = initNum; i < limitNum; i++) {
  //   // !!!  
  //   // (-1)  for !
  //   if (i > totalCount.current - 1) break;

  //   //  ! ///
  //   selData.push(baseData[i]);
  // } //////////// for :   ///////////

  /********************************************* 
     : searchFn
     :    
         !
     :  ( )
  *********************************************/
  const searchFn = () => {
    //      !
    // ->     !
    setKeyword({
      cta: $("#cta").val(),
      kw: $("#stxt").val(),
    });
  }; /////// searchFn  ////////////////////

  // DOM   ///////
  useEffect(() => {
    //    ///
    window.scrollTo(0, 0);
  }); //// useEffect /////////

  //   /////////////////
  return (
    <div className="board-wrap">
      {
        // [1]    : mode -> "L" ////
        mode === "L" && (
          <List
            selData={selData} //   
            setMode={setMode} //   setter
            selRecord={selRecord} //  
            //    /////
            pageNum={pageNum} //   getter
            setPageNum={setPageNum} //   setter
            unitSize={unitSize} //  
            totalCount={totalCount} //   
            pgPgSize={pgPgSize} //   
            pgPgNum={pgPgNum} //   
            // ,     /////
            searchFn={searchFn} // 
            keyword={keyword} //   getter
            setKeyword={setKeyword} //   setter
            order={order} //  
            setOrder={setOrder} //   setter
            sortCta={sortCta} //   getter
            setSortCta={setSortCta} //   setter
            initVariables={initVariables} // 
          />
        )
      }

      {
        // [2]   : mode -> "R" ///
        mode === "R" && (
          <Read
            setMode={setMode} //   setter
            selRecord={selRecord} //  
          />
        )
      }

      {
        // [3]   : mode -> "W" ///
        mode === "W" && (
          <Write
            setMode={setMode} //   setter
            totalCount={totalCount} //   
            setPageNum={setPageNum} //   setter
            pgPgNum={pgPgNum} //   
            initVariables={initVariables} // 
          />
        )
      }

      {
        // [4]   : mode -> "M" ///
        mode === "M" && (
          <Modify
            setMode={setMode} //   setter
            selRecord={selRecord} //  
            totalCount={totalCount} //   
            setPageNum={setPageNum} //   setter
            pgPgNum={pgPgNum} //   
          />
        )
      }
    </div>
  );
}

export default Board;
