import React from "react";

//   ////
import $ from "jquery";

function Modify({setMode, selRecord, totalCount, setPageNum, pgPgNum}) {
  // setMode -    setter
  // selRecord -  
  // totalCount -    (  1!)
  // setPageNum -   setter (  )
  // pgPgNum -    (  1)

  //    
  const selData = selRecord.current;

  //     //////
  const submitFn = () => {
    // 
    let title = $(".subject").val().trim();
    // 
    let content = $(".content").val().trim();
    // trim()   !

    // (1)  
    // - ,    !
    if (title === "" || content === "") {
      alert("Insert title and content!");
      return;
    } /// if /////

    // (2)   //////
    else {
      // 1)    ////////////
      // 1-1)    
      let localData = localStorage.getItem("board-data");

      // 1-2) JSON.parse()  
      localData = JSON.parse(localData);

      // 2)    idx()
      let currIdx = selData.idx;
      console.log("idx:", currIdx);

      // 3)     find 
      //  idx    
      localData.find((v) => {
        if (v.idx === currIdx) {
          // , 
          v.tit = title;
          v.cont = content;
          //  DB   
          //     
          //       !
          //   !
          v.mdate = new Date().toJSON().substr(0, 10);

          //    !
          return true;
        } /// if ///
      }); /// find ///

      // 4)    
      localStorage.setItem("board-data", JSON.stringify(localData));

      // 5)     
      setMode("L");
    } /// else /////
  }; ////////// submitFn  //////////////

  // [   ] /////
  const deleteFn = () => {
    //   /////
    if (window.confirm("Are you sure you want to delete?")) {
      // ""  true  !
      // console.log('~!');

      // 1)    ////////////
      // 1-1)    
      let localData = localStorage.getItem("board-data");

      // 1-2) JSON.parse()  
      localData = JSON.parse(localData);

      // 2)    idx()
      let currIdx = selData.idx;
      console.log("idx:", currIdx);

      // 3)     some() 
      //  idx  !
      // find()  some()  boolean 
      //  find() some() return true  !
      localData.some((v, i) => {
        if (v.idx === currIdx) {
          //   : i  
          localData.splice(i, 1);

          //  true  !
          return true;
        } /// if ///
      }); ////// some ///////

      // 4)    
      localStorage.setItem("board-data", JSON.stringify(localData));

      // 5)   1 ////
      totalCount.current--;

      // 6)   
      setPageNum(1);

      // 7)    
      pgPgNum.current = 1;

      // 8)     
      setMode("L");
    } /// if :confirm true ///
  }; ///////// deleteFn  ////////////////

  //   /////////////////////
  return (
    <>
      <h1 className="tit"></h1>
      <table className="dtblview readone">
        <caption></caption>
        <tbody>
          <tr>
            <td></td>
            <td>
              <input type="text" className="name" size="20" readOnly={true} defaultValue={selData.unm} />
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <input type="text" className="subject" size="60" defaultValue={selData.tit} />
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <textarea className="content" cols="60" rows="10" defaultValue={selData.cont}></textarea>
            </td>
          </tr>
          {/* <tr>
            <td>Attachment</td>
            <td></td>
          </tr> */}
        </tbody>
      </table>
      <br />
      <table className="dtbl btngrp">
        <tbody>
          <tr>
            <td>
              <button onClick={submitFn}></button>
              <button onClick={deleteFn}></button>
              <button
                onClick={() => {
                  //  ('L') 
                  setMode("L");
                }}>
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </>
  );
}

export default Modify;
