import React, { useState } from "react";
import "../../scss/pages/join.scss";
import { Link, useNavigate } from "react-router-dom";
import { initData } from "../../js/func/mem_fn";

function Join() {
  const goPage = useNavigate();

  //   
  const [userId, setUserId] = useState("");
  const [pwd, setPwd] = useState("");
  const [chkPwd, setChkPwd] = useState("");
  const [userName, setUserName] = useState("");
  const [email, setEmail] = useState("");

  //    
  const [userIdError, setUserIdError] = useState(false);
  const [pwdError, setPwdError] = useState(false);
  const [chkPwdError, setChkPwdError] = useState(false);
  const [userNameError, setUserNameError] = useState(false);
  const [emailError, setEmailError] = useState(false);

  //    
  const [isValid, setIsValid] = useState(false);

  //  
  const msgId = [
    " 5  .",
    "  .",
    "   ",
  ];

  const msgEtc = {
    pwd: ", ,   5  .",
    confPwd: "  .  .",
    req: "    .",
    email: "  ",
  };

  //    
  const [idMsg, setIdMsg] = useState(msgId[0]);

  //   
  const changeUserId = (e) => {
    const val = e.target.value;
    const valid = /^[A-Za-z0-9+]{5,}$/;
    setUserId(val);
    validateFields();

    if (valid.test(val)) {
      initData();
      let memData = JSON.parse(localStorage.getItem("mem-data") || "[]");
      const isT = memData.some((v) => v.uid === val);

      if (isT) {
        setIdMsg(msgId[1]);
        setUserIdError(true);
      } else {
        setUserIdError(false);
        setIdMsg(msgId[2]); //  
      }
    } else {
      setIdMsg(msgId[0]);
      setUserIdError(true);
    }

    setUserId(val);
    validateFields();
  };

  //   
  const changePwd = (e) => {
    const val = e.target.value;
    const valid = /^.*(?=^.{5,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;

    setPwdError(!valid.test(val));
    setPwd(val);
    validateFields();
  };

  //    
  const changeChkPwd = (e) => {
    const val = e.target.value;
    setChkPwdError(pwd !== val);
    setChkPwd(val);
    validateFields();
  };

  //    
  const changeUserName = (e) => {
    const val = e.target.value;
    setUserNameError(val === "");
    setUserName(val);
    validateFields();
  };

  //   
  const changeEmail = (e) => {
    const val = e.target.value;
    const valid = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    setEmailError(!valid.test(val));
    setEmail(val);
    validateFields();
  };

  //   
  const validateFields = () => {
    const valid =
      userId &&
      pwd &&
      chkPwd &&
      userName &&
      email &&
      !userIdError &&
      !pwdError &&
      !chkPwdError &&
      !userNameError &&
      !emailError;

    setIsValid(valid); //   
    console.log("Is valid:", valid); //    
  };

  //  
  const onSubmit = (e) => {
    e.preventDefault();
    if (isValid) {
      // isValid   ,   .
      initData();
      let memData = JSON.parse(localStorage.getItem("mem-data") || "[]");
      let newData = {
        idx: (memData.length > 0 ? Math.max(...memData.map((v) => v.idx)) : 0) + 1,
        uid: userId,
        pwd: pwd,
        unm: userName,
        eml: email,
      };

      memData.push(newData);
      localStorage.setItem("mem-data", JSON.stringify(memData));

      document.querySelector(".submit").innerText = "  ";
      setTimeout(() => {
        goPage("/login");
      }, 1000);
    } else {
      alert("  ");
    }
  };

  return (
    <div className="join-page">
      <div className="join-top">
        <h2 className="join-title"></h2>
      </div>

      <ul className="slide-text">
        <li className="slide">    </li>
        <li className="slide">  ,  </li>
        <li className="slide">     </li>
      </ul>

      <form onSubmit={onSubmit} className="join-form">
        <ul>
          <li className="join-id">
            <label></label>
            <input
              type="text"
              maxLength="15"
              placeholder=" "
              value={userId}
              onChange={changeUserId}
            />
            {userIdError && (
              <div className="msg" style={{ color: "#fe5b16" }}>
                {idMsg}
              </div>
            )}
            {!userIdError && userId && (
              <div className="msg" style={{ color: "#00c471" }}>
                {msgId[2]}
              </div>
            )}
          </li>
          <li className="join-pass">
            <label></label>
            <input
              type="password"
              maxLength="20"
              placeholder=" "
              value={pwd}
              onChange={changePwd}
            />
            {pwdError && (
              <div className="msg" style={{ color: "#fe5b16" }}>
                {msgEtc.pwd}
              </div>
            )}
          </li>
          <li className="join-pass-confirm">
            <label> </label>
            <input
              type="password"
              maxLength="20"
              placeholder="  "
              value={chkPwd}
              onChange={changeChkPwd}
            />
            {chkPwdError && (
              <div className="msg" style={{ color: "#fe5b16" }}>
                {msgEtc.confPwd}
              </div>
            )}
          </li>
          <li className="join-name">
            <label></label>
            <input
              type="text"
              maxLength="8"
              placeholder=" "
              value={userName}
              onChange={changeUserName}
            />
            {userNameError && (
              <div className="msg" style={{ color: "#fe5b16" }}>
                {msgEtc.req}
              </div>
            )}
          </li>
          <li className="join-name">
            <label></label>
            <input type="text" maxLength="50" placeholder=" " value={email} onChange={changeEmail} />
            {emailError && (
              <div className="msg" style={{ color: "#fe5b16" }}>
                {msgEtc.email}
              </div>
            )}
          </li>
          <li>
            <button type="submit" className={`submit ${isValid ? "valid" : ""}`}>
              
            </button>
          </li>
          <ul className="join-already">
            <li> ?</li>
            <li>
              <Link to="/login"></Link>
            </li>
          </ul>
        </ul>
      </form>
    </div>
  );
}

export default Join;
